<template>
  <div>
    <h1>QR.CSS</h1>
    <div class="align-h">
      <ul>
        <li>QR.CSS: The modern, responsive CSS framework for mobile-first web design.</li>
        <li>Build beautiful, responsive websites with ease using QR.CSS.</li>
        <li>QR.CSS: Your go-to CSS framework for creating stunning mobile-first experiences.</li>
        <li>Experience the power of QR.CSS: A responsive CSS framework tailored for modern web development.</li>
        <li>QR.CSS: Craft responsive, mobile-first websites with simplicity and elegance.</li>
      </ul>

    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      cssCode: `
        /* Enter your CSS styles here */
        background-color: #007bff; /* Background color */
        color: #ffffff; /* Text color */
        font-size: 30px;
        font-family: Arial;
        padding: 20px;
      `,
      backgroundColor: '#007bff',
      textColor: '#ffffff',
      fontSize: 30,
      fontFamily: 'Arial'
    }
  },
  methods: {
    generateImage () {
      const canvas = this.$refs.canvas
      const ctx = canvas.getContext('2d')

      // Clear canvas
      ctx.clearRect(0, 0, canvas.width, canvas.height)

      // Apply CSS-like properties
      ctx.fillStyle = this.backgroundColor // Background color
      ctx.fillRect(0, 0, canvas.width, canvas.height)

      ctx.fillStyle = this.textColor // Text color
      ctx.font = this.fontSize + 'px ' + this.fontFamily
      ctx.fillText('Hello, CSS to Image!', 50, 100)

      // Convert canvas to image and download
      const dataURL = canvas.toDataURL('image/png')
      const link = document.createElement('a')
      link.download = 'css_to_image.png'
      link.href = dataURL
      link.click()
    },
    applyCSS () {
      const styles = this.cssCode.replace(/\n/g, ' ').trim() // Remove newlines
      const styleElement = document.createElement('style')
      styleElement.textContent = styles
      document.head.appendChild(styleElement)

      // Extract styles from the applied CSS
      const computedStyles = getComputedStyle(document.body)
      this.backgroundColor = computedStyles.backgroundColor
      this.textColor = computedStyles.color
      this.fontSize = parseFloat(computedStyles.fontSize)
      this.fontFamily = computedStyles.fontFamily.split(',')[0] // Get primary font family
    }
  },
  mounted () {
    // Initialize CSS editor with default CSS styles
    this.applyCSS()
  }
}
</script>

